import './style.css'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

import Stats from 'three/examples/jsm/libs/stats.module.js';
import * as Smoke from './Scripts/Smoke.js'
import { gsap } from "gsap";
import *  as Scrollbar from "smooth-scrollbar/dist/smooth-scrollbar.js";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js'

//Gsap Smooth Scroll
gsap.registerPlugin(ScrollTrigger);

// Setup
const scroller = document.querySelector('.scroller');
export const bodyScrollBar = Scrollbar.init(scroller,
    { damping: 0.1, delegateTo: document, alwaysShowTracks: true });

bodyScrollBar.addListener(ScrollTrigger.update);

ScrollTrigger.defaults({ scroller: scroller });



ScrollTrigger.scrollerProxy(".scroller", {
    scrollTop(value) {
        if (arguments.length) {
            bodyScrollBar.scrollTop = value;
        }
        return bodyScrollBar.scrollTop;
    }
});
//------------------------------------------------------------------------------


// Debug
let stats = new Stats();
document.body.appendChild(stats.dom)

//------------------------------------------------------------------------------    
// Data
const nearPlane = 0.01;
const farPlane = 100.0;
const pixelRatio = 2;
var mixer, Loaded;


/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight,
}

window.addEventListener('resize', () => {
    UpdateResizing();
})
ScrollTrigger.addEventListener('revert', UpdateResizing)

function UpdateResizing() {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, pixelRatio))
}
//------------------------------------------------------------------------------
// Canvas
var canvas = document.getElementById('scene_01')

// Scene
const scene = new THREE.Scene()
//ADDING BACKGROUND AND FOG
scene.background = null;

var groupRotation = new THREE.Group();
scene.add(groupRotation);

// Objects
const geometry = new THREE.TorusGeometry(.7, .2, 16, 100);

// Materials

const material = new THREE.MeshLambertMaterial()
material.color = new THREE.Color(0xff0000)

// Mesh
const sphere = new THREE.Mesh(geometry, material)
//scene.add(sphere)

// Lights


const directionalLight = new THREE.DirectionalLight(0xffffff, 3);
directionalLight.position.set(0, 5, 5);
groupRotation.add(directionalLight)


const amlight = new THREE.AmbientLight(0xffffff, 1);
amlight.castShadow = false;
amlight.position.set(0, 0, 0);
groupRotation.add(amlight);


// const pointLight = new THREE.PointLight(0xffffff, 3)
// pointLight.position.x = 2
// pointLight.position.y = 3
// pointLight.position.z = 4
// scene.add(pointLight)


const lighthelper = new THREE.DirectionalLightHelper(directionalLight, .1);
groupRotation.add(lighthelper);

//------------------------------------------------------------------------------

/**
 * Camera
 */
// Base camera
var camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, nearPlane, farPlane)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 2
scene.add(camera)

// Controls
//const controls = new OrbitControls(camera, canvas)
//controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
    alpha: true,
})
//canvas.appendChild(renderer.domElement);
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, pixelRatio))
renderer.toneMapping = THREE.ACESFilmicToneMapping;
renderer.toneMappingExposure = 1.0;
renderer.shadowMap.enabled = true;
renderer.autoClear = true;
renderer.outputEncoding = THREE.sRGBEncoding;
renderer.physicallyCorrectLights = true;

/**
 * BackGroundEffects
 */
Smoke.init(scene);



new RGBELoader()
    .setPath('https://echend.github.io/delco/')
    .load('container_free_Env.hdr', function (texture) {

        texture.mapping = THREE.EquirectangularReflectionMapping;

        //scene.background = texture;
        scene.environment = texture;
    });



// ----------- Instantiate a loader
const loader = new GLTFLoader();

// Optional: Provide a DRACOLoader instance to decode compressed mesh data
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath('three/examples/js/libs/draco/');
loader.setDRACOLoader(dracoLoader);


// Load a glTF resource
loader.load(
    // resource URL
    'https://echend.github.io/delco/gltf/gltf.gltf',
    // called when the resource is loaded
    function (gltf) {
        scene.add(gltf.scene);
        console.log(gltf.scene);
        gltf.animations; // Array<THREE.AnimationClip>
        gltf.scene; // THREE.Group
        gltf.scenes; // Array<THREE.Group>
        gltf.cameras; // Array<THREE.Camera>
        gltf.asset; // Object


        camera = gltf.cameras[0];
        groupRotation = gltf.scene.children[1];

        UpdateResizing();
        mixer = new THREE.AnimationMixer(gltf.scene)
        var action = mixer.clipAction(gltf.animations[0]);
        mixer.setTime(0);
        createAnimation(mixer, action, gltf.animations[0]);
        bodyScrollBar.offset.y = 10;
        Loaded = true;
    },
    // called while loading is progressing
    function (xhr) {

        console.log((xhr.loaded / xhr.total * 100) + '% loaded');

    },
    // called when loading has errors
    function (error) {

        console.log('An error happened');
        console.log(error);
    }
);




function createAnimation(mixer, action, clip) {
    let proxy = {
        get time() {
            return mixer.time;
        },
        set time(value) {
            action.paused = false;
            mixer.setTime(value);
            action.paused = true;
        }
    };

    let scrollingTL = gsap.timeline({
        scrollTrigger: {
            trigger: renderer.domElement,
            start: "top top",
            end: "+=10000",
            pin: true,
            markers: false,
            invalidateOnRefresh: true,
            scrub: 3,
            onUpdate: function () {
                camera.updateProjectionMatrix();
            },
            onEnter: function () {
                mixer.setTime(0);
                action.play();
            }
        }
    });

    scrollingTL.to(proxy, {
        time: clip.duration,
        repeat: 0
    });

}



/**
 * Animate
 */

const clock = new THREE.Clock()

const tick = () => {

    const elapsedTime = clock.getElapsedTime()

    // Update objects
    //sphere.rotation.y = .5 * elapsedTime

    // Update Orbital Controls
    //controls.update()

    // Render
    renderer.render(scene, camera)

    stats.update();

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()



//----------------------------Mouse Movement Animation

window.addEventListener("mousemove", onMouseMove);

function onMouseMove(e) {
    const x = THREE.MathUtils.mapLinear(
        e.clientY,
        0,
        window.innerHeight,
        -0.3,
        0.3
    );
    const y = THREE.MathUtils.mapLinear(
        e.clientX,
        0,
        window.innerWidth,
        -0.3,
        0.3
    );

    gsap.to(groupRotation.rotation, {
        y: y * .1,
        x: x * .1,
        duration: 3.0,
        ease: "power1.out"
    });


}

